.nav {
  padding-top: 15px;
  position: fixed;
  z-index: 999;
  display: flex;
  width: 100vw;
  flex-direction: column;
  gap: 15px;
  border-bottom: #bbb solid 1px;
  background-color: #fff;
}

/* nav menu top */
.navMenuTop {
  height: 30px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 15px;
}

.navTitle {
  font-weight: 600;
}

.back {
  width: 18px;
}

.qrIcon {
  width: 30px;
}

/* nav menu bottom */
.navMenuBottom {
  display: flex;
}

.navActive {
  border-bottom: #000 solid 2px;
}

.navElement {
  padding: 10px 27px;
}

.navElement a {
  color: #bbb;
}
.navActive a {
  color: #000 !important;
}
