.idcMain {
  margin-top: 50px;
  display: flex;
  
  flex-direction: column;
  width: 90%;
  position: relative;
  
}

.idcWrapper {
  height: 450px;
  display: flex;
  flex-direction: column;
  align-items: center;
  border: #919191 solid 1px;

  background-color: #fff;
  outline: none;
}

.idcWarning {
  display: flex;
  align-items: center;
  position: relative;
  overflow: hidden;
  border-radius: 10px 10px 0 0;
  width: 100%;
  height: 12px;
  background-color: #466fff;
  color: #fff;
  font-size: 8px;
  gap: 12px;
}

.idcWarnTxt {
  font-weight: 600;
  white-space: nowrap;
  animation: scroll 25s linear infinite;
}

.idcBlank {
  padding: 50px;
}

.idcContainer {
  margin-top: 5px;
  display: flex;
  justify-content: space-between;
  width: 70%;
}

.idcImg {
  
  width: 135px;
  margin-top: 20px;
}

.idcDetail {
  width: 100%;
  margin-top: 30px;
  display: flex;
  flex-direction: column;
  gap: 10px;
  font-size: 14px;
}

.idcDetail .name {
  
  color: #000;
  font-size: 22px;
  font-weight: 600;
}

.idcNameContainer {
  margin-top: 5px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.idcHideBtn {
  display: flex;
  align-items: center;
  gap: 4px;
  outline: none;
  border: none;
  width: 44px;
  height: 21px;
  flex-grow: 0;
  padding: 0 2px;
  border-radius: 10px;
  background-color: #bbb;
}

.idcEllipse {
  
  border-radius: 100px;
  width: 17px;
  height: 17px;
  flex-grow: 0;
  background-color: #fff;
}

.idcHidden {
  font-size: 10px;
  color: #fff;
}

.idcRemaining {
  display: flex;
  flex-direction: column;
  gap: 10px;
  width: 70%;
  font-size: 12px;
  color: rgb(85, 211, 199);
  padding: 15px 0;
}



.idcBottomTxt {

  position: absolute;
  bottom: 60px;
  bottom: 60px;
  text-align: center;
  width: 70%;
  font-size: 14px;
  padding-bottom: 10px;
}

.idcBottomBtn {
  position: absolute;
  bottom: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 13px;
  color: #fff;
  background-color: #000;
  border: none;
  width: 70%;
  height: 32px;
  border-radius: 100px;
}

@keyframes scroll {
  0% {
    transform: translateX(0);
  }
  85% {
    transform: translateX(calc(-208px * 4));
  }
  100% {
    transform: translateX(calc(-208px * 4));
  }
}
