html,
body {
  -webkit-user-select:none;
  -moz-user-select:none;
  -ms-user-select:none;
  user-select:none;
  overflow: hidden;
  margin: 0;
  background-color: #fff;
}

a {
  text-decoration: none;
  
  
}
.main {
  overflow: hidden;
  width: 100%;

  bottom: 0px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  /* padding: 0px 40px; */
  height: calc(100vh - 80px);
}

.st-button {
  position: fixed;
  bottom: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  
  width: 90%;
  height: 35px;
  border-radius: 10px;
}

.progressbar {
  height: 2px;

  background-color: #bbb;
}

.settings-nav{


  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px;
}

.settings-nav-arrow{
  width: 18px;
  

}

.settings-nav-name{
  font-weight: 600;
}

.settings-list{

  display: flex;
  flex-direction: column;
  
}

.settings-items{
  background-color: white;
  border: none;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px 15px;
  font-size: 16px;
  
  
}
.main a{
  display: flex;
  justify-content: center;
  align-items: center;
}
