.notice {
  padding-top: 2px;
  width: 90%;
  height: 55px;
  flex-grow: 0;
  border-radius: 10px;
  background-color: #c5d484;
  display: flex;
  gap: 5px;
  align-items: center;
  position: absolute;
  bottom: 78px;
}

.text {
  margin: 13px;
  padding-right: 30px;

  letter-spacing: 1px;
  font-size: 16px;
  font-weight: bold;
  color: #fff;
}

.icon {
  padding-left: 18px;
  align-self: flex-end;

  height: 45px;
  flex-grow: 0;
}
